.tabs {
  padding: 0 8px;
}

.tabs a:focus,
.tabs button:focus {
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

.tabs__inner {
  position: relative;
}

.tabs__list {
  border: solid #ccc;
  border-width: 0 1px 1px 1px;
  list-style: none;
  text-align: left;
  display: none;
}

.tabs__list--mobile-btn {
  position: relative;
  background: #fff;
  height: 50px;
  width: 100%;
  border: solid;
  border-width: 2px 1px 0 1px;
  border-color: #0092ff #ccc;
  padding: 0 0 0 16px;
  text-align: left;
}

.tabs__list--mobile-btn:focus {
  outline: 1px dotted #000;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

.tabs__list--mobile-btn__text {
  font-weight: 600;
}

.tabs__list--mobile-btn__dropdown-icon {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  padding: 12px;
  border: solid #ccc;
  border-width: 0 0 1px 1px;
}

.tabs__list--mobile-btn__dropdown-icon:before {
  font-size: 10px;
}

.tabs__list--item {
  display: block;
  margin-bottom: 0;
}

.tabs__list--item-link {
  display: block;
  text-decoration: none;
  color: #414042;
  padding: 16px 16px;
}

.tabs__list--item-link:focus,
.tabs__list--item-link:hover,
.tabs__list--item-link:active {
  color: #414042;
}

.tabs__list--item-link.active {
  font-weight: 600;
  outline: none;
}

.tabs__list--item-link.active:focus {
  outline: #000 dotted 1px;
}

.tabs__module--container {
  margin-top: 24px;
}

.tabs__module--container .tabs__module {
  display: none;
}

.tabs__module--container .tabs__module.active {
  display: block;
}

@media (min-width: 320px) and (max-width: 767px) {
  .tabs__list {
    margin: 0;
    padding: 0;
    position: absolute;
    width: 100%;
    z-index: 5001;
    background: #fff;
  }

  .tabs__list--item-link:not(.active) {
    border-top: 1px solid #ccc;
  }

  .tabs__list--item-link.active {
